
import Vue from "vue";
import store from "@/store";
import Food from "@/ts/classes/Food";

export default Vue.extend({
    name: "FoodPageForm",

    data() {
        return {
            formBrand: "",
            formName: "",
            formSize: "",
            formCount: 0,
        };
    },

    computed: {
        brandState(): boolean {
            return this.formBrand !== null && this.formBrand !== "";
        },
        nameState(): boolean {
            return this.formName !== null && this.formName !== "";
        },
        sizeState(): boolean {
            return this.formSize !== null && this.formSize !== "";
        },
        state(): boolean {
            return this.brandState && this.nameState && this.sizeState;
        }
    },

    methods: {
        show(): void {
            this.$bvModal.show("formModal");
        },
        submitForm(): void {
            store.dispatch("foods/addFood", new Food(
                this.formBrand,
                this.formName,
                this.formSize,
                this.formCount
            ));

            this.clearForm();
            this.$emit("done-form");
        },
        clearForm(): void {
            this.formBrand = "";
            this.formName = "";
            this.formSize = "";
            this.formCount = 0;
        }
    }
});
